.die .face {
	height: 60px;
	width: 60px;
	position: absolute;
	background: #3e6fa0
}

.die .dot {
	width: 8px;
	height: 8px;
	background: #fff;
	border-radius: 50%;
	display: block;
	position: absolute
}

.die .six {
	-webkit-transform: rotateY(0deg) translateZ(30px);
	transform: rotateY(0deg) translateZ(30px)
}

.die .one {
	-webkit-transform: rotateX(180deg) translateZ(30px);
	transform: rotateX(180deg) translateZ(30px)
}

.die .five {
	-webkit-transform: rotateY(90deg) translateZ(30px);
	transform: rotateY(90deg) translateZ(30px)
}

.die .two {
	-webkit-transform: rotateY(-90deg) translateZ(30px);
	transform: rotateY(-90deg) translateZ(30px)
}

.die .three {
	-webkit-transform: rotateX(90deg) translateZ(30px);
	transform: rotateX(90deg) translateZ(30px)
}

.die .four {
	-webkit-transform: rotateX(-90deg) translateZ(30px);
	transform: rotateX(-90deg) translateZ(30px)
}

@-webkit-keyframes rotate1 {
	0% {
		-webkit-transform: rotateX(90deg) rotateY(90deg) rotateZ(-90deg) translateX(0);
		transform: rotateX(90deg) rotateY(90deg) rotateZ(-90deg) translateX(0)
	}

	10% {
		-webkit-transform: rotateX(-90deg) rotateY(-90deg) rotateZ(90deg) translateX(0);
		transform: rotateX(-90deg) rotateY(-90deg) rotateZ(90deg) translateX(0)
	}

	30% {
		-webkit-transform: rotateX(90deg) rotateY(180deg) rotateZ(-90deg) translateX(0);
		transform: rotateX(90deg) rotateY(180deg) rotateZ(-90deg) translateX(0)
	}

	50% {
		-webkit-transform: rotateX(-180deg) rotateY(90deg) rotateZ(360deg) translateX(0);
		transform: rotateX(-180deg) rotateY(90deg) rotateZ(360deg) translateX(0)
	}

	70% {
		-webkit-transform: rotateX(90deg) rotateY(-180deg) rotateZ(-90deg) translateX(0);
		transform: rotateX(90deg) rotateY(-180deg) rotateZ(-90deg) translateX(0)
	}

	90% {
		-webkit-transform: rotateX(-90deg) rotateY(-90deg) rotateZ(90deg) translateX(0);
		transform: rotateX(-90deg) rotateY(-90deg) rotateZ(90deg) translateX(0)
	}

	100% {
		-webkit-transform: rotateY(180deg);
		transform: rotateY(180deg)
	}
}

